import { useTranslation } from 'react-i18next';
import { swedishCurrencyFormatter } from '../../utils/locale-utils';

interface Props {
  estateAddress: string;
  estateArea: string;
  estateCity: string;
  estatePrice: number | undefined;
  priceText: string;
}

export default function EstateInfo({ estateAddress, estateArea, estateCity, estatePrice, priceText }: Props) : JSX.Element {
  const { t } = useTranslation('estate-info');

  return (
    <div aria-label={ t('section-description') } className="estate-info">
      <div>
        <span className="estate-address" data-testid="estateAddress"> { estateAddress } </span><br/>
        <span className="estate-area" data-testid="estateArea"> { estateArea }, { estateCity } </span>
      </div>
      <div className="estate-pricing">
        <span className="starting-price"> { t(priceText) } </span> <br/>
        <span className="estate-price" data-testid="estatePrice">
          { estatePrice !== undefined ? swedishCurrencyFormatter.format(estatePrice) : t('no-price-set') }
        </span>
      </div>
    </div>
  );
}
