interface IApplicationOptions {
  baseUrl: string;
  theme: string;
}

declare global {
  interface Window { applicationOptions: IApplicationOptions; }
}

class ApplicationOptions implements IApplicationOptions {
  private defaults = {
    baseUrl: 'https://localhost:3000',
    theme: 'erik-olsson'
  };

  private get cleanedWindowOptions() : IApplicationOptions {
    const options = window.applicationOptions;
    for(const key of Object.getOwnPropertyNames(options)) {
      // 2022-01-17 KJ We need to access this by key here but typescript
      // doesn't know what it returns
      // eslint-disable-next-line
      // @ts-ignore
      const value = options[key];

      if(typeof value === 'undefined') {
        // 2022-01-17 KJ Another case of key access to a property which
        // typescript doesn't like
        // eslint-disable-next-line
        // @ts-ignore
        delete options[key];
      }
    }

    return options;
  }

  private get applicationOptions(): IApplicationOptions {
    return {...this.defaults, ...this.cleanedWindowOptions } as IApplicationOptions;
  }

  get baseUrl() : string {
    return this.applicationOptions.baseUrl;
  }

  get theme() : string {
    return this.applicationOptions.theme;
  }
}

export default new ApplicationOptions();
