import { useTranslation } from 'react-i18next';

interface Props {
  image: string;
  title: string;
  description: string;
  linkText: string;
  linkUrl: string;
}

export default function Tip({ image, title, description, linkText, linkUrl }: Props): JSX.Element {

  const { t } = useTranslation('estate-link');

  return (
    <div className='tip' data-testid='tip'>
      <img src={ image } role="presentation" alt='' />
      <h3 className='title'> { title } </h3>
      <p className='description'> { description } </p>
      <a href={ linkUrl } data-testid='link' rel='noopener' aria-label={ t('link-label', { title: title }) }> { linkText } </a>
    </div>
  );
}
