import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import { ReactComponent as NotificationIcon } from './notification.svg';
import { ReactComponent as ProfileIcon } from './profile.svg';

interface Props {
  numberOfNewNotifications: number,
  handleProfileClick(): void
}

export default function TopNavBar({ numberOfNewNotifications, handleProfileClick }: Props): JSX.Element {
  const { t } = useTranslation('header');

  return (
    <nav className="top-nav-bar" data-testid="top-nav-bar">
      <Link to="/">
        <img src="/images/logotype.svg" className="logo" alt={ t('logo-link-text') } />
      </Link>
      <div className="icons">
        <Link to="notifications">
          <NotificationIcon
            className={ numberOfNewNotifications > 0 ? 'new-notifications' : '' }
            role='img'
            aria-label={ t('notifications-link-text', { count: numberOfNewNotifications }) }
            data-testid="notification-icon" />
        </Link>
        <button onClick={ handleProfileClick } data-testid="profile-button">
          <ProfileIcon role='img' aria-label={ t('profile-button-text') } />
        </button>
      </div>
    </nav>
  );
}
