import { Suspense, useEffect, useState } from 'react';
import { Route, Routes } from 'react-router-dom';
import { Notification } from './services/generated-api-client';
import Home from './pages/Home';
import Login from './pages/Login';
import Loader from './components/loader';
import startMockApi from './mock-api';
import ApiClient from './services/api-client';
import EstateInfo from './components/EstateInfo/EstateInfo';
import TopNavBar from './components/TopNavBar/TopNavBar';
import BottomNavBar from './components/BottomNavBar/BottomNavBar';

function App(): JSX.Element {
  const [notifications, setNotifications] = useState<Notification[]>([]);

  useEffect(() : (() => void) => {
    // Start the mock api on mount and then shutdown it on unmount
    const server = startMockApi();
    return () : void => {
      server.shutdown();
    };
  }, []);

  useEffect(() : void => {
    ApiClient
      .authenticated
      .getNotifications()
      .then(response => setNotifications(response.data));
  }, []);

  return (
    <Suspense fallback={ <Loader /> }>
      <header className="main-header">
        <TopNavBar numberOfNewNotifications={ notifications.length }
          handleProfileClick={ function(): void { return; } } />
        <EstateInfo estateAddress="Hornsgatan 119"
          estateArea="Södermaln"
          estateCity="Stockholm"
          estatePrice={ 3500000 }
          priceText='starting-price'/>
      </header>
      <main>
        <Suspense fallback={ <Loader /> }>
          <Routes>
            <Route path="/*" element={ <Home /> } />
            <Route path="/login" element={ <Login /> } />
          </Routes>
        </Suspense>
      </main>
      <BottomNavBar />
    </Suspense>
  );
}

function SuspenseWrapper(): JSX.Element {
  return (
    <Suspense fallback={ <Loader /> }>
      <App />
    </Suspense>
  );
}

export default SuspenseWrapper;
