import Pretender from 'pretender';
import { ApplicationData, Notification } from './services/generated-api-client';

export default function startMockApi(): Pretender {
  const server = new Pretender(function() {
    // Add APIs we don't want mocked here
    this.get('/Services/Public/IApplication/GetSomeRealDataAsync', this.passthrough);

    // Add APIs we want to mock here
    this.get('/Services/Public/IApplication/GetApplicationDataAsync', () => {
      const data = { Version: '0.0.1' } as ApplicationData;
      return [200, { 'Content-Type': 'application/json' }, JSON.stringify(data)];
    });

    this.get('/Services/Authenticated/IUserData/GetNotificationsAsync', () => {
      const data = [
        { Id: '1', Text: 'Välkommen till mina sidor!' },
        { Id: '2', Text: 'Nytt bud på din bostad' }
      ] as Notification[];
      return [200, { 'Content-Type': 'application/json' }, JSON.stringify(data)];
    });
  });

  return server;
}
