import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import { swedishCurrencyFormatter } from '../../utils/locale-utils';

interface Props {
  image: string;
  address: string;
  priceStatus: string;
  price: number;
  priceDifference: number;
  date: string | null;
  route: string;
}

export default function EstateLink({image, address, priceStatus, price, priceDifference, date, route}: Props): JSX.Element {

  const { t } = useTranslation('estate-link');

  function getPriceDifference(): JSX.Element | void {
    if(priceDifference !== 0) {
      const arrow = priceDifference > 0 ? '↑' : '↓';
      const label = priceDifference > 0 ? t('price-increased', { difference: priceDifference })
        : t('price-decreased', { difference: Math.abs(priceDifference) });
      return <span className='price-difference' aria-label={ label } data-testid='price-difference'>{ arrow }{ priceDifference }%</span>;
    }
  }

  return (
    <Link to={ route } className='estate-link' data-testid='estate-link'>
      <img role="presentation" src={ image } alt='' />
      <div className='information'>
        <div className='address'> { address } </div>
        <span className='price-text' data-testid="price-text"> { t(priceStatus) } { swedishCurrencyFormatter.format(price) } </span>
        { getPriceDifference() }
        { date ? <div className='date-sold' data-testid='estate-sold'> { t('sold') } { date } </div>
          : <div className='in-progress' data-testid='estate-status'> { t('in-progress') } </div> }
      </div>
    </Link>
  );
}
